body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.center-circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.second-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-second;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%)rotate(0);
}

.minute-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s linear infinite rotate-minute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%)rotate(0);
}

@keyframes rotate-second {
  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

@keyframes rotate-minute {
  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

.stopwatch--speed-up .second-hand {
  animation-duration: 10s;
}

.stopwatch--speed-up .minute-hand {
  animation-duration: 600s;
}

